import { Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { VersionManageService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateVNComponent } from "../../core/UI_FormatComponent";
import ManageVersionModifyComponent from "./ModifyComponent";

export default function ManageVersionIndexComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [typeOrderBy, setTypeOrderBy] = useState<boolean>(false);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string | undefined>(undefined);
    const column = [
        new TableColumn({
            title: "STT", key: "stt", disabled: true, render: (data: any, row: any, index: any) =>
                Fn.RenderSTT(data, row, index, pageInfo)
        }),
        new TableColumn({
            title: "Loại", key: "type", render: (data: number) => {
                let color = "";
                let value = "";
                if (data === 1) {
                    color = "red";
                    value = "Frontend";
                }

                if (data === 2) {
                    color = "green";
                    value = "Backend";
                }

                if (data === 3) {
                    color = "blue";
                    value = "Database";
                }

                return <Tag color={color}>{value}</Tag>;
            }
        }),
        new TableColumn({
            title: "Phiên bản", key: "version", render: (data: any) =>
                <span>
                    {`v${data.major}.${data.minor}.${data.patch}-${data.preRelease}`}
                </span>
        }),
        new TableColumn({
            title: "Tên", key: "name", render: (value: string, row: any) =>
                <MyButton value={value} type="link" onClick={() => { handleClickDetail(row.key) }} />
        }),
        new TableColumn({ title: "Ghi chú", key: "message" }),
        new TableColumn({
            title: "Ngày phát hành", key: "dateRelease", render: (data: string, row: any) =>
                <UIFormatDateVNComponent value={data} />
        })
    ];

    const [data, setData] = useState([]);
    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        VersionManageService.GetList(paginationRequest)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false))
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickDetail(key: string) {
        setVisibleEdit(true);
        setKeyEdit(key);
    }

    function handleClickAdd() {
        setVisible(true);
    }

    return (
        <MyBox
            title="Quản lí phiên bản"
            isLoading={isLoading}>
            <MyButton
                type="primary"
                value="Thêm mới"
                onClick={handleClickAdd} />

            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loadData={loadData}
                loading={isLoading}
                typeOrderBy={typeOrderBy}
                setTypeOrderBy={setTypeOrderBy} />

            <ManageVersionModifyComponent
                visible={visible}
                setVisible={setVisible}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading} />

            <ManageVersionModifyComponent
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                loadData={loadData}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                keyEdit={keyEdit}
                setKeyEdit={setKeyEdit} />
        </MyBox>
    );
}
