import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from 'react';
import Swal from "sweetalert2";
import { Fn } from "../../../helper/function";
import { VersionManageService } from "../../../services/adminService";
import { MyFormItem } from "../../core/Input";
import { MyButton } from "../../core/MyButton";
import './logVersion.css';
var moment = require('moment');

export default function ManageVersionModifyComponent(props: any) {
    const options = [
        {
            label: 'Frontend',
            key: 'fr',
            value: 1
        },
        {
            label: 'Backend',
            key: 'be',
            value: 2
        },
        {
            label: 'Database',
            key: 'db',
            value: 3
        }
    ]

    const [type, setType] = useState(1);
    const [majorVersion, setMajorVersion] = useState(0);
    const [minorVersion, setMinorVersion] = useState(0);
    const [pathVersion, setPathVersion] = useState(0);
    const [preReleaseVersion, setReReleaseVersion] = useState('');
    const [dateRelease, setDateRelease] = useState(null);
    const [name, setName] = useState('');
    const [_message, setMessage] = useState('');

    const { isLoading, setIsLoading, keyEdit, loadData, setVisible, setKeyEdit, visible } = props;

    function handleClickButton() {
        setIsLoading(true);
        let body = {
            type: type,
            version: {
                major: majorVersion,
                minor: minorVersion,
                patch: pathVersion,
                preRelease: preReleaseVersion
            },
            name: name,
            message: _message,
            dateRelease: dateRelease
        };
        if (keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        VersionManageService.Add(body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccess(rs);
                    handleOnClose();
                    loadData();
                }
            })
            .finally(() => setIsLoading(false))
    }

    function edit(body: any) {
        VersionManageService.GetDetail(keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestamp(rs, body);
                    VersionManageService.Edit(keyEdit, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                handleOnClose();
                                loadData();
                            }
                        })
                        .finally(() => setIsLoading(false));
                }
                else setIsLoading(false);
            })
    }

    const loadDataDetail = useCallback(() => {
        if (keyEdit !== undefined) {
            setIsLoading(true);
            VersionManageService.GetDetail(keyEdit)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        setType(rs.data.type);
                        setMajorVersion(rs.data.version.major);
                        setMinorVersion(rs.data.version.minor);
                        setPathVersion(rs.data.version.patch);
                        setReReleaseVersion(rs.data.version.preRelease);
                        setName(rs.data.name);
                        setMessage(rs.data.message);
                        setDateRelease(moment(rs.data.dateRelease));
                    }
                })
                .finally(() => setIsLoading(false));
        }
    }, [keyEdit, setIsLoading]);

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail]);

    function handleOnClose() {
        setVisible(false);
        if (keyEdit !== undefined) {
            setKeyEdit(undefined);
        }
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn có muốn xóa không?"
        })
            .then((res) => {
                if (res.isConfirmed) {
                    doDelete();
                }
            })
    }

    function doDelete() {
        setIsLoading(true);
        VersionManageService.GetDetail(keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    VersionManageService.Delete(keyEdit, Fn.GetTimestamp(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                loadData();
                                handleOnClose();
                            }
                        })
                        .finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            open={visible}
            onClose={handleOnClose}
            title={keyEdit !== undefined ? "Chỉnh sửa log version" : "Thêm log version mới"}>
            <Form layout="vertical">
                <MyFormItem.Select
                    label="Loại"
                    options={options}
                    value={type}
                    setValue={setType} />

                <Form.Item label="Phiên bản">
                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Major"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={majorVersion}
                        setValue={setMajorVersion} />

                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Minor"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={minorVersion}
                        setValue={setMinorVersion} />

                    <MyFormItem.NumberColSpan
                        className="row-version"
                        label="Patch"
                        colSpan1={8}
                        colSpan2={16}
                        min={0}
                        max={1000000}
                        value={pathVersion}
                        setValue={setPathVersion} />

                    <Row className="row-version">
                        <Col span={8}>
                            <span>PreRelease</span>
                        </Col>
                        <Col span={16}>
                            <MyFormItem.Input
                                value={preReleaseVersion}
                                setValue={setReReleaseVersion} />
                        </Col>
                    </Row>
                </Form.Item>

                <MyFormItem.Input
                    label="Tên"
                    value={name}
                    setValue={setName} />

                <MyFormItem.DatePicker
                    label="Ngày phát hành"
                    value={dateRelease}
                    setValue={setDateRelease} />

                <MyFormItem.TextArea
                    label="Ghi chú"
                    value={_message}
                    setValue={setMessage} />

                <MyButton
                    onClick={handleClickButton}
                    isLoading={isLoading}
                    htmlType="submit"
                    type="success"
                    value={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    onClick={handleClickDelete}
                    isLoading={isLoading}
                    type="danger"
                    value="Xóa" />
            </Form>
        </Drawer>
    );
}